
body {
    background-image: url("https://images.unsplash.com/photo-1536424868245-13b715e11e71?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1650&q=80รง");
    background-size: cover; 

  }

  .box {
    display: inline-block;       
    border: 1px solid black;
    border-radius: 20px 20px;
    width: 15px;
    height: 15px;
    margin-left: 0px;
    margin-bottom: 0px;
    transition: transform .2s;
  }
  
  .off {
    background-color: transparent;
  }
  
  .on {
    background-color: blue;
  }
    
  .box:hover { 
    background: #00CCFF;
    transform: scale(1.7);
  }
  
  .center {
    margin-left: 5%;
    display: table;
    margin-top: 10px;
  }
  
  .grid {
    line-height: 0;
    margin-left: 5%;
    box-shadow: 0px 0px 20px white;
    margin-top: 10px;
    border: 10px;
  }
  .listStyle {
  color: black;
  }
  h1, h2, h3, h4 {
    color: white;
    font: "Lucida Sans Unicode", "Lucida Grande", sans-serif;
    margin-left: 2%;
    margin-bottom: 0%;
    text-align: center;
    width: 30%;
  }
  
  .credit {
    margin-top: 5%;
    margin-bottom: 2%;  
    margin-left: 1%;
  }

  .button {
    background-color: green; /* Green */
    border: 2px;
    color: white;
    margin: 5px;
    margin-bottom: 10px;
    margin-right: 10px;
    margin-left: 10px;
    padding: 5px 5px 5px 5px;
    text-align: center;
    display: inline-block;
    font-size: 16px;
    transition: transform .2s;
    box-shadow: 0px 0px 5px white;
  }
  
  .button:hover {
    background-color: black;
    transform: scale(1.2);
  }

  .placeHolder {     
    margin-right: 5%;
    float: right;
    vertical-align: middle;
    display: table-cell;
  }

  .infoSize {
    font-size: large;
  }

  .modal {
    font-size: 12px;
  }
  .modal > .header {
    width: 100%;
    border-bottom: 1px solid gray;
    font-size: 18px;
    text-align: center;
    padding: 5px;
  }
  .modal > .content {
    width: 100%;
    padding: 10px 5px;
  }
  .modal > .actions {
    width: 100%;
    padding: 10px 5px;
    margin: auto;
    text-align: center;
  }
  .modal > .close {
    cursor: pointer;
    position: absolute;
    display: block;
    padding: 2px 5px;
    line-height: 20px;
    right: -10px;
    top: -10px;
    font-size: 24px;
    background: #ffffff;
    border-radius: 18px;
    border: 1px solid #cfcece;
  }

  